*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  font-family: $font-primary;
  height: 100%;
  min-height: 100vh;
  background: url('../static/assets/ui/QuizBackground.png') center;
  background-size: cover;
}

.app__box {
  @include lg {
  }
  @include sm {
  }
}

.mode__container--active {
  @extend .app__box;
  overflow: auto;
  margin: 48px 0;
  padding: 0 48px 20px;
  border-radius: $border-radius;
  background-color: $color-primary;
  box-shadow: 0px 4px 4px rgb(0 0 0 / 25%), 0px 4px 30px #ff0000;
}

.mode-info__heading {
  padding: 25px 16px 22px;
}

.text__md {
  font-size: $font-size-secondary;
  font-weight: 500;
  line-height: 133%;
}
.text__lg {
  font-size: $font-size-primary;
  font-weight: 600;
  line-height: 123%;
  &--centered {
    @extend .text__lg;
    text-align: center;
  }
}

#swquiz-app {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 32px;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

@include xlg {
  .text {
  }
  .mode__container--active {
    padding: 0 32px 32px;
    margin: 32px 0;
  }
}
@include lg {
  body > div {
    padding: 25px 20px 25px 20px;
  }
  .text {
    &__md,
    &__lg {
      font-size: $font-size-secondary / 1.125;
    }
  }

  .mode__container--active {
    padding: 0 32px 16px;
  }
}
@include md {
  .text {
    &__md,
    &__lg {
      font-size: $font-size-secondary / 1.375;
    }
  }
  .mode__container--active {
    box-shadow: 0px 2px 2px rgb(0 0 0 / 25%),
      2px 2px 20px rgb(255 0 0 / 90%);
  }
}

@include sm {
  body > div {
    padding: 15px 10px 15px 10px;
  }
  #swquiz-app {
    padding: 0 16px;
  }
  .text {
    &__md,
    &__lg {
      font-size: $font-size-secondary / 1.5;
    }
  }
  .mode__container--active {
    margin: 16px 0;
    padding: 0 16px 16px;
  }
}

@include xsm {
  .text {
    &__md,
    &__lg {
      font-size: $font-size-primary / 2;
    }
  }
  #swquiz-app {
    min-height: 100vh;
    height: unset;
  }
}
